  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor2 {
  width: 40%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor3 {
  max-width: 120rem;
  margin: 0 0 0 70rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  color: #FFFFFF;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
  color: #FFFFFF;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-azul {
  background-color: #0A516D;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 5rem;
}
.boton-azul:hover {
  background-color: #084056;
  cursor: pointer;
}

/** UI **/
.header {
  background-color: #2B2726;
  padding: 1rem 0 3rem 0;
  margin-bottom: 0;
}

.copyright {
  color: #2B2726;
  text-align: center;
  margin: 0;
  padding: 0;
}

footer {
  margin: 0;
  padding: 0;
}
footer .p {
  margin: 0;
  padding: 0;
}

.barra {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 3rem;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  margin-bottom: 0;
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/** Menu de hambuerguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.no-webp .inicio {
  background-image: url(../img/responsive.jpg);
}
@media (min-width: 768px) {
  .no-webp .inicio {
    background-image: url(../img/fondo.jpg);
  }
}

.webp .inicio {
  background-image: url(../img/responsive.webp);
}
@media (min-width: 768px) {
  .webp .inicio {
    background-image: url(../img/fondo.webp);
  }
}

.inicio, .login, .registrarse {
  margin: 0;
  padding: 0;
  background-position: center center;
  background-size: cover;
}
.inicio h1, .login h1, .registrarse h1 {
  font-weight: normal;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .inicio, .login, .registrarse {
    min-height: 70rem;
  }
}
.inicio h1, .login h1, .registrarse h1 {
  margin: 0;
  font-weight: bold;
  text-align: left;
}

.primero {
  padding-top: 10rem;
}

.info p {
  margin: 1rem;
}

.contenedor2 .save {
  margin: 0;
  margin-top: 5rem;
  padding-bottom: 10rem;
}
@media (min-width: 768px) {
  .contenedor2 .save {
    margin-top: 10rem;
  }
}

.save {
  text-decoration: underline;
  text-align: center;
}
@media (min-width: 768px) {
  .save {
    margin-left: 30rem;
  }
}

.logout {
  text-align: center;
}
.logout a {
  color: #000;
}
@media (min-width: 768px) {
  .logout {
    margin: 8rem 10rem 0 0;
    text-align: end;
  }
  .logout a {
    color: #2B2726;
  }
}

.registro {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .registro {
    margin-right: 10rem;
  }
}

.contenedor2 {
  align-items: center;
}
.contenedor2 h1, .contenedor2 p {
  font-size: 1.5rem;
  font-weight: 1rem;
}
.contenedor2 .primero {
  margin-left: 6rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .contenedor2 h1, .contenedor2 p {
    font-size: 2.5rem;
    font-weight: bold;
  }
  .contenedor2 .primero {
    padding-top: 15rem;
    font-size: 3.5rem;
    font-weight: bold;
    margin-left: 15rem;
  }
}

.centrar {
  text-decoration: underline;
  margin: 0 0 0 5rem;
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .centrar {
    margin: 0 0 0 15rem;
    padding: 0 0 10rem 0;
  }
}

a {
  color: #2B2726;
  font-weight: bold;
  text-decoration: none;
}

.no-webp .login {
  background-image: url(../img/fondo2.jpg);
}

.webp .login {
  background-image: url(../img/fondo2.webp);
}

.login a {
  display: block;
  color: #FFFFFF;
}

.elemento label {
  color: #FFFFFF;
  display: flex;
  font-weight: bold;
  font-size: 2.5rem;
}
.elemento .imagenes {
  max-width: 7%;
}
.elemento input {
  width: 100%;
  padding: 1rem 0;
  color: #FFFFFF;
  background: transparent;
  border: none;
  border-bottom: 0.1rem solid #FFFFFF;
  margin-bottom: 1rem;
}

.perfil {
  max-width: 40%;
  padding-top: 2rem;
  margin: 0 0 0 20rem;
}

fieldset {
  border: none;
}

.centrado {
  text-align: center;
  margin: 1.5rem 0;
}

.centrado2 {
  margin-left: 26.5rem;
}

.formulario2 .elemento label {
  color: #FFFFFF;
  display: flex;
  font-weight: bold;
  font-size: 2.5rem;
}
.formulario2 .elemento .imagenes {
  max-width: 10%;
}
.formulario2 .elemento input {
  width: 70%;
  padding: 1rem 0;
  color: #FFFFFF;
  background: transparent;
  border: none;
  border-bottom: 0.1rem solid #FFFFFF;
  margin-bottom: 1rem;
}

.primero2 {
  padding: 10rem 0 3rem 8rem;
  font-size: 7rem;
}

.primero3 {
  padding: 10rem 0 3rem 0;
  font-size: 6rem;
  font-weight: normal;
}

.centrado3 {
  margin: 1rem 0 3rem 14rem;
}

.centrado4 {
  margin: 1rem 0 3rem 12rem;
}

.blanco {
  color: #FFFFFF;
  margin-left: 16rem;
  text-decoration: underline;
}

.no-webp .registrarse {
  background-image: url(../img/registro.jpg);
}

.webp .registrarse {
  background-image: url(../img/registro.webp);
}